@import '../helpers/_helpers';

.btn-2 {
	position: relative;
	display: block;
	margin: 10px;
	width: 150px;
	text-align: center;
	text-decoration: none;
	padding: 5px;
	color: #0047AB;
	border: 3px solid #0047AB;
	transition: all 0.3s ease;
	box-shadow: 0px 4px 10px 2px rgba(black, 0.2);
	&:before{
		position: absolute;
		content: '';
		width: 0%; height: 100%;
		background: #0047AB;
		top: 0;  left: auto; right: 0;
		z-index: -1;
		transition: all 0.3s ease;
	}

	&:hover{
		color: white;
		box-shadow:none;
		&:before{
			position: absolute;
			content: '';
			width: 100%; height: 100%;
			background: #0047AB;
			top: 0; left: 0; right: 0;
			z-index: -1;
		}
	}
	&:focus {
		outline: none;
	}
}
